/*
biomebot part worker
==================================

part worker

# 

## 1. 

providerbotIdsyncCache()
indexedDBbotModules
botIdmain worker, part workers

## 2 provider -> worker {type: 'deploy'}

tfidf{type:'deployed'}

## 3. worker -> provider {type: 'deployed'}

providerpartdeployed
deployedavatar
partdeployed4

## 4. provider -> main {type: 'run', summon:summon}
main summon{ON_SUMMON}{ON_START}
part{type:'activate', part:'partName'}


## 5. provider -> main worker {type: 'input', message:message}


*/
import {part} from './part.core';

self.onmessage = (event) => {
  const action = event.data;
  // console.log('partWorker recieved', action);
  switch (action.type) {
    case 'deploy': {
      part.deploy(action).then((result) => {
        if (result) {
          self.postMessage({type: 'deployed', moduleName: part.moduleName});
        } else {
          self.postMessage({type: 'deployError', moduleName: part.moduleName});
        }
      });

      break;
    }

    case 'kill': {
      part.kill();
      self.close();
      break;
    }

    default:
      throw new Error(`partWorker: invalid action ${action.type}`);
  }
};
